#contact {
  position: relative;
  padding-bottom: 100px;

  &:after {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    background: #F6F9FC;
    z-index: -10;
    transform: skewY(-9deg);
  }

  h2 {
    margin-bottom: 64px;
  }

  .social {
    display: block;
    text-align: center;

    a {
      display: inline-block;
      margin-bottom: 16px;
      font-size: 18px;
      border: 1px solid $orange;
      padding: 8px;
      border-radius: 4px;
      color: $orange;
      transition: all 0.3s;

      &:hover {
        background: $orange;
        color: white;
      }

      & + a {
        margin-left: 8px;
      }
    }
  }
}
