html {
  font-family: 'Open Sans', 'Helvetica Neue', 'Helvetica', Arial, sans-serif;
}

a {
  color: $grey;
  text-decoration: none;

  &:hover {
    color: $orange;
  }
}

h1 {
  font-size: 70px;
  color: $orange;
  letter-spacing: 3px;
  text-transform: uppercase;
  font-weight: bold;

  @media screen and (max-width: 600px) {
    font-size: 60px;
  }

  @media screen and (max-width: 480px) {
    text-align: center;
  }
}

h2 {
  display: block;
  font-family: 'Droid Serif', serif;
  position: relative;
  text-align: center;
  font-size: 20px;
  margin-bottom: 32px;
  margin-top: 60px;
  line-height: 1.6;
  padding: 0 16px;
  font-weight: normal;
  color: #666;

  &:after {
    content: '';
    display: block;
    position: absolute;
    bottom: -16px;
    left: 50%;
    height: 3px;
    width: 50px;
    background: #FD746C;
    transform: translate3d(-50%, 0, 0);
  }

  @media screen and (max-width: 480px) {
    font-size: 16px;
    margin-top: 36px;
  }
}

em {
  color: $orange;
  font-style: normal;
}

body {
  position: relative;
  padding-top: 60px;
  overflow-x: hidden;
}
