.down-arrow {
  position: relative;
  display: block;
  width: 28px;
  height: 25px;
  font-size: 0;
  margin: 0 auto;
  cursor: pointer;

  .block {
    display: inline-block;
    width: 3px;
    height: 20px;
    background: #CCC;
    transition: all 0.3s;

    & + .block {
      margin-left: 3px;
    }

    &:nth-child(2n) {
      transform: translateY(6px);
    }

    &:nth-child(3) {
      transform: translateY(12px);
    }
  }

  &:hover {
    .block {
      background: $orange;

      &:nth-child(2n) {
        transform: translateY(8px);
      }
      &:nth-child(3) {
        transform: translateY(16px);
      }
    }
  }
}
